import * as React from 'react'
import Layout from '../../components/layoutGallery'
import { Link, graphql } from 'gatsby'
import { arrowNextBackground, arrowPrevBackground, arrowIconNext, arrowIconPrev, galleryItem} from './gallery.module.css'
import { GatsbyImage } from 'gatsby-plugin-image'
import Slider from "react-slick";
import 'slick-carousel/slick/slick.css'
import 'slick-carousel/slick/slick-theme.css'


const CustomPrevArrow = ({  onClick }) => (
  <div className={arrowPrevBackground} onClick={onClick} >
    <div className={arrowIconPrev}> &lt; </div>
  </div>
);

const CustomNextArrow = ({  onClick }) => (
  <div  onClick={onClick} className={arrowNextBackground}>
    <div className={arrowIconNext}> &gt; </div>
  </div>
);
const Portfolio = ({data}) => {


  const settings = {
    className: "center",
    centerMode: true,
    infinite: true,
    centerPadding: "60px",
    slidesToShow: 3,
    speed: 500,
    prevArrow: <CustomPrevArrow />,
    nextArrow: <CustomNextArrow />,
    dots: true,
    responsive: [
      {
        breakpoint: 768,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
          infinite: true,
          dots: true,
          vertical: true,
          verticalSwiping: true,
        }
      },
    ]

  };


  return (
    <Layout pageTitle="Portfolio">

      <>
     

        <Slider {...settings}>
        {
        data.allMdx.nodes.map(node => (
          <div key={node.id}>
          <Link to={`/portfolio/${node.frontmatter.slug}`} style={{textDecoration: 'none', color: '#13263D'}}   state={{
    modal: true
  }}>
          <article key={node.id} className={galleryItem} >
            <GatsbyImage image={node.frontmatter.cover.childImageSharp.gatsbyImageData} imgStyle={{ objectFit: 'COVER' }} style={{ height: "100%", width: "100%" }} />
            <h3>
              
                {node.frontmatter.title}
              
            </h3>
            
          </article>
          </Link>
          </div>
        ))
      }
        </Slider>
      

      

      </>
    </Layout>
  )
}
export const query = graphql`
  query {
    allMdx(sort: { frontmatter: { date: DESC }}) {
      nodes {
        frontmatter {
          date(formatString: "MMMM D, YYYY")
          title
          slug
          cover {
            childImageSharp {
              gatsbyImageData(transformOptions: {fit: CONTAIN})
            }
          }
        }

        id
      }
    }
  }
`

export const Head = () => <title>Portfolio</title>

export default Portfolio